import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { environment } from '../../environments/environment';
import { Title } from '@angular/platform-browser';
import { MovieDBService } from '../services/movie-db.service';
import { debounceTime, takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';


@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss']
})
export class SearchComponent implements OnInit, OnDestroy {
  defaultImage = environment.placeholderImg;
  isLoading = false;

  search: any;
  itemList: any;
  $search = new Subject<string>();
  unsubscribe = new Subject<void>();

  constructor(
    public DBService: MovieDBService,
    public router: Router,
    private titleService: Title
  ) {}

  ngOnInit() {
    this.titleService.setTitle('Buscar filme, série ou ator...');

    this.getDiscover();

    this.$search
      .pipe(takeUntil(this.unsubscribe), debounceTime(300))
      .subscribe(respostaComDelay => {

        if (!respostaComDelay.length) {
          this.getDiscover();
        }

        if (respostaComDelay.length > 2) {
          this.getMovies(respostaComDelay);
        }
      });
  }

  ngOnDestroy() {
    this.unsubscribe.next();
    this.unsubscribe.complete();
  }

  getMovies(value: string) {

    if (this.isLoading) {
      return;
    }
    this.isLoading = true;

    this.DBService.getkeywords(this.search = value)
      .subscribe ((res: any) => {
        this.isLoading = false;
        this.itemList = this.DBService.formatMovies(res.results)
      }, () => this.isLoading = false);
  }

  getDiscover() {
    if (this.isLoading) {
      return;
    }
    this.isLoading = true;
    this.DBService.getDiscover('movie', {page: 1})
      .subscribe((res: any) => {
        this.isLoading = false;
        this.itemList = this.DBService.formatMovies(res.results);
      }, () => this.isLoading = false);
  }
  
  goToMovieDetails(movie) {
    this.router.navigate(['/movie', movie.id]);
  }

}
